html, body {
  height: 100%;
  margin: 3px;
}

body {
  background-color: #fff;
  font-family: sans-serif;
  overflow: hidden;
}

#container {
  grid-template-rows: 30px calc(100% - 50px);
  height: 100%;
  display: grid;
}

#header {
  grid-row: 1;
}

.button-row-label {
  font-size: small;
}

#split {
  flex-direction: row;
  grid-row: 2;
  display: flex;
}

#run-area {
  background: #333;
  width: 40%;
  height: 100%;
}

#canvas-container {
  resize: both;
  border: 1px solid #000;
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;
  position: relative;
  overflow: hidden;
}

#canvas-container canvas {
  object-fit: contain;
  width: 100%;
  max-width: 512px;
  height: 100%;
  max-height: 512px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#image-canvas {
  z-index: 1;
}

#blockly-area {
  text-align: center;
  background: #fff;
  width: 60%;
}

#column-resizer {
  cursor: col-resize;
  border: 1px solid #0000;
  width: 6px;
  height: 100%;
  margin: 0;
  padding: 0;
  display: block;
  top: 0;
  right: 0;
}

#footer {
  grid-row: 3;
}

#drag {
  z-index: 999;
  pointer-events: none;
  cursor: col-resize;
  clip-path: inset(0% 19%);
  -webkit-user-select: none;
  user-select: none;
  background-color: #fff;
  width: 22px;
  position: relative;
  top: 45%;
  left: -9px;
}

